export default {
	props:{
		list:{
			type: Array,
			required: true,
			default:()=>[]
		},
	},
	methods:{
		gridItemClick(url){
			if(url=='/pages/tabbar/my'){
				uni.switchTab({
					url:url
				})
				return
			}
			if(url=='/pages/order/order-list'){
				if(!global.isLogin){
					uni.navigateTo({
						url:'/pages/user/login'
					})
					return
				}
				uni.navigateTo({
					url:url
				})
				return
			}
			
			uni.navigateTo({
				url:url
			})
		}
	}
}
